import React, {Component} from 'react';
import _ from 'lodash';

export default class Box extends React.Component{
    render(){
        return (
            <div className="box" >
                {this.props.title && <div className="box__title">
                    {_.startCase(this.props.title)}
                </div>}

                <div>
                {this.props.children}

                </div>
            </div>
        )
    }
}
